.movieInfo {
    padding-top: 20px;
}

.description {
    display: flex;
    gap: 30px;
    padding-bottom: 10px;
    padding-left: 10px;
    border-bottom: 1px solid rgb(0, 0, 0);
    box-shadow: 0px 2px 0px 0px rgba(59, 58, 58, 0.75);
}

.button {
    font-weight: 600;
    margin-left: 10px;
    margin-bottom: 10px;
    display: flex;
    gap: 10px;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    border-radius: 10%;
    border: 3px solid black;
}

.titleMovie {
    font-size: 32px;
}

.scoreInfo {
    font-size: 18px;
    line-height: 1.3;
}

.scoreNumber {
    margin-left: 5px;
}

.overviewText {
    font-size: 18px;
    line-height: 1.3;
}

.genresList {
    font-size: 18px;
    line-height: 1.3;
    display: flex;
    gap: 15px;
    list-style: none;
}

.addTitle {
padding-left: 10px;
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 0;
}

.addLinks {
    padding-left: 30px;
    font-size: 16px;
    font-weight: 700;
    border-bottom: 1px solid rgb(0, 0, 0);
    box-shadow: 0px 2px 0px 0px rgba(59, 58, 58, 0.75);
}

.errorText {
    font-size: 16px;
    font-weight: 700;
    line-height: 1.3;
}